___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --white: #fff;
  --black: #000;
  --bg: #f8f8f8;
  --grey: #999;
  --dark: #1a1a1a;
  --light: #e6e6e6;
  --wrapper: 1000px;
  --blue: #00b0ff;
}
::-webkit-scrollbar {
  display: none;
}
::-webkit-scrollbar-thumb {
  display: none;
}

body {
  background-color: var(--bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
  background-size: cover;
  background-repeat: none;
  overflow: hidden;
}

.wrapper {
  position: relative;
  left: 50%;
  max-width: 1024px;
  height: 100vh;
  transform: translate(-50%, 0);
  padding: 0.7rem;
}

.container {
  position: relative;
  top: 42%;
  left: 50%;
  width: 95%;
  height: 75%;
  overflow: hidden;
  background-color: white;
  transform: translate(-50%, -50%);
  padding: 2rem;
  border-radius: 5px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

body {
  font-family: "Inter", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #edf3f6;
}
.text-input {
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.chat {
  grid-column: span 6 / span 6;
  height: 100vh;
  padding: 0.7rem;
}
.rounded-content {
  border-radius: 0.5rem;
  background: white;
  min-height: 100%;
  min-height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 1rem;
}
form {
  position: relative;
  width: 100%;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.mde-text {
  height: 400px;
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.submit-button {
  display: inline-block;
  margin: 1rem 0;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: white;
  background-color: #0070f3;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.submit-button:hover {
  background-color: #0051cc;
}
.response-display {
  margin-top: 1rem;
  padding: 0.5rem;
}
.container .right {
  position: relative;
  float: left;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}
.container .right .chat {
  position: relative;
  display: none;
  overflow: auto;
  padding: 0 35px 92px;
  padding-bottom: 12rem;
  height: calc(100% - 48px);
  /* justify-content: flex-end; */
  flex-direction: column;
}
.container .right .chat.active-chat {
  display: block;
  display: flex;
}
.container .right .chat.active-chat .bubble {
  transition-timing-function: cubic-bezier(0.4, -0.04, 1, 1);
}
.container .right .write {
  position: absolute;
  bottom: 0px;
  left: 30px;
  height: 42px;
  padding-left: 8px;
  border: 1px solid var(--light);
  background-color: #eceff1;
  width: calc(100% - 58px);
  border-radius: 5px;
}
.container .right .write input {
  font-size: 16px;
  float: left;
  width: 80%;
  height: 40px;
  padding: 0 10px;
  color: var(--dark);
  border: 0;
  outline: none;
  background-color: #eceff1;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
}
.bubble {
  max-width: 43.75rem;
}

.container .right .write .write-link.send:before {
  position: absolute;
  right: 0;
  float: left;
  width: 14.9%;
  height: 42px;
  content: "";
  background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/382994/send.png");
  background-repeat: no-repeat;
  background-position: center;
  border: 0.7px solid rgb(196, 196, 196);
  border-radius: 5px;
  transition: all 0.2s ease-in;
}
.container .right .bubble {
  font-size: 16px;
  position: relative;
  display: inline-block;
  clear: both;
  margin-bottom: 8px;
  padding: 13px 14px;
  vertical-align: top;
  border-radius: 5px;
}
.container .right .bubble:before {
  position: absolute;
  top: 19px;
  display: block;
  width: 8px;
  height: 6px;
  content: " ";
  transform: rotate(29deg) skew(-35deg);
}
.container .right .bubble.assistant {
  float: left;
  color: var(--white);
  background-color: var(--blue);
  align-self: flex-start;
  -webkit-animation-name: slideFromLeft;
  animation-name: slideFromLeft;
}
.container .right .bubble.assistant:before {
  left: -3px;
  background-color: var(--blue);
}
.container .right .bubble.me {
  float: right;
  color: var(--dark);
  background-color: #eceff1;
  align-self: flex-end;
  -webkit-animation-name: slideFromRight;
  animation-name: slideFromRight;
}
.container .right .bubble.me:before {
  right: -3px;
  background-color: #eceff1;
}
.container .right .conversation-start {
  position: relative;
  width: 100%;
  margin-bottom: 27px;
  text-align: center;
}
.container .right .conversation-start span {
  font-size: 14px;
  display: inline-block;
  color: var(--grey);
}
.container .right .conversation-start span:before,
.container .right .conversation-start span:after {
  position: absolute;
  top: 10px;
  display: inline-block;
  width: 30%;
  height: 1px;
  content: "";
  background-color: var(--light);
}
.container .right .conversation-start span:before {
  left: 0;
}
.container .right .conversation-start span:after {
  right: 0;
}

.container .right .conversation-start span:before {
  left: 0;
}
.container .right .conversation-start span:after {
  right: 0;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.animate-spin {
  animation: spin 1s linear infinite;
  color: rgb(250, 250, 250);
}

.watermark {
  color: rgba(128, 128, 128, 0.168);
  opacity: 10;
  font-size: 15rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  transform-origin: center;
  font-weight: 800;
}

.auth-button {
  background: #00b0ff;
  cursor: pointer;
  color: #fff;
  border: none;
  padding: 10px 30px;
  border-radius: 5px;
}

.auth-button__hover:hover {
  background: #0051cc;
}

.wrapper-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 640px) {
  .container .right .chat {
    padding: 0;
  }
}
